const nombre = document.querySelector('#nombre');
const correo = document.querySelector('#correo');
const telefono = document.querySelector('#telefono');
const asunto = document.querySelector('#asunto');
const btnEnviar = document.querySelector('.boton-formulario');
const main = document.querySelector('.vacio');
const spiner = document.querySelector('.animacion');
const form = document.querySelector('.form');

window.addEventListener('scroll', function() {
    let movimiento = document.querySelector('#animado');
    let posicionObj = movimiento.getBoundingClientRect().top;
    let tamañoDePantalla = window.innerHeight / 2;

    if (posicionObj < tamañoDePantalla) {
        movimiento.style.animation = 'mover 1s ease-out'
    }
})


btnEnviar.addEventListener('click', function(e) {
    e.preventDefault();
    nombreContacto();
})


function nombreContacto() {
    const nombreCon = nombre.value;
    if (nombreCon === '' || nombreCon.length < 3) {
        mostrarAlerta('Invalid Name');
    } else {
        emailContaco();
    }
}

function numeroContaco() {
    const numeroCon = telefono.value.trim();
    if (!/^[0-9]+$/.test(numeroCon) || numeroCon.length < 10 || numeroCon === '') {
        mostrarAlerta('Invalid Number');
    } else {
        mensajeContaco();
    }
}

function emailContaco() {
    const emailCon = correo.value.trim();
    const validar = /^([\da-z_\.-]+)@([\da-z\.-]+)\.([a-z\.]{2,6})$/;
    if (!validar.exec(emailCon)) {
        mostrarAlerta('Invalid email');
    } else {
        numeroContaco();
    }

}

function mensajeContaco() {
    const mensajeCon = asunto.value.trim();
    if (mensajeCon === '' || mensajeCon.length < 2) {
        mostrarAlerta('Write a Message');
    } else {
        spinner();
        setTimeout(() => {
            mostrarCorrecto('Message Sent Successfully');
            limpiarFormulario();
        }, 1000);

    }

}



function mostrarAlerta(mensaje) {

    const alerta = document.createElement('DIV');
    alerta.textContent = mensaje;
    alerta.classList.add('alerta');
    alerta.classList.add('error');
    main.appendChild(alerta);
    //Desaparezca despues de tres segundos
    setTimeout(() => {
        alerta.remove();
    }, 5000);
}

function spinner() {
    const divSpinner = document.createElement('div');
    divSpinner.classList.add('sk-chase');

    divSpinner.innerHTML = `
  <div class="sk-chase-dot"></div>
  <div class="sk-chase-dot"></div>
  <div class="sk-chase-dot"></div>
  <div class="sk-chase-dot"></div>
  <div class="sk-chase-dot"></div>
  <div class="sk-chase-dot"></div>
    `;
    spiner.appendChild(divSpinner);
    //Desaparezca despues de tres segundos
    setTimeout(() => {
        divSpinner.remove();
    }, 1000);

}

function mostrarCorrecto(mensaje) {

    const alerta = document.createElement('DIV');
    alerta.textContent = mensaje;
    alerta.classList.add('alerta');
    alerta.classList.add('correcto');
    main.appendChild(alerta);
    //Desaparezca despues de tres segundos
    setTimeout(() => {
        alerta.remove();
    }, 5000);
}

function limpiarFormulario() {
    form.reset();
}